// export default AboutUs;

import React from 'react'
import Link from 'next/link'
import Image from 'next/image'
import MaskGroupImg from '../public/images/Mask group.png'
import menImg from '../public/images/men.png'
import Group from '../public/images/Group 50.png'

export const AboutUs = () => {
  return (
    <div className="bg-white min-h-screen px-6 pt-28">
      {/* Heading Row */}
      <div className="flex flex-col md:flex-row items-center justify-center gap-6 md:gap-20 mb-12">
        <h1 className="text-black font-medium text-xl">About Us</h1>
        <h1 className="text-black font-extrabold font-Poppins text-3xl text-center md:text-left">
          FASHION THAT EMPOWERS
        </h1>
      </div>

      {/* Images Row */}
      <div className=" w-full flex flex-col items-center gap-6 md:flex-row md:justify-center md:gap-12">
        {/* Mask Image */}
        <Image
          className="w-[200px] md:w-[300px] h-auto"
          src={MaskGroupImg}
          alt="mask"
        />

        {/* Men Image */}
        <Image
          className="w-[150px] md:w-[200px] h-auto"
          src={menImg}
          alt="men"
        />

        {/* Group Image */}
        <Image
          className="w-[220px] md:w-[300px] h-auto pt-4 md:pt-10"
          src={Group}
          alt="group"
        />
      </div>
    </div>
  )
}

export default AboutUs;

